.message {
    width: 400px;
    padding: 20px;
    margin: 10px;
    background-color: blueviolet;
    border: 1px solid black;
    border-radius: 10px;
}

.wrapper {
    display: flex;
    background-color: black;
}

.avatar {
    display: flex;
    justify-content: center;
    flex-shrink: 0;
    width: 100px;
    height: 100px;
    overflow: hidden;
    border-bottom-right-radius: 15px;
    background-color: blueviolet;
}

.avatar img {
    align-self: flex-end;
    display: block;
    border: 1px solid black;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    max-width: 50px;
    max-height: 50px;
}

.message_text {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid black;
    color: white;
    background-color: black;
    padding: 10px;
    width: 300px;
}

.message_text:before {
    content: "";
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: blueviolet;
    transform: rotate(45deg);
    top: -7px;
    left: -7px;
}

.message_text:after {
    content: "";
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: blueviolet;
    transform: rotate(45deg);
    top: -7px;
    right: -7px;

}

.test {
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: blueviolet;
    transform: rotate(45deg);
    bottom: -7px;
    right: -7px;
}

.message_text div:nth-child(3) {
    align-self: flex-end;
}

.message_text div:nth-child(2) {
    align-self: center;
}
